import React from "react";
import PropTypes from "prop-types";
import { List } from 'antd';
import Helmet from 'react-helmet';
// Components
import Link from "gatsby-link";

const Tags = ({ pathContext, data }) => {
  const { tag } = pathContext;
  const { edges, totalCount } = data.allMarkdownRemark;
  const tagHeader = `发现${totalCount}个携带「${tag}」标签的文章`;
  const Posts = edges.filter(edge => !!edge.node.frontmatter.date);
  return (
    <div>
      <Helmet title={`${tag} - Lighted mirror`} />
      <h1>{tagHeader}</h1>
      <div className="home-container">
        <List
          itemLayout="horizontal"
          dataSource={Posts}
          renderItem={item => {
            const { path, title, dsct, author } = item.node.frontmatter;
            return (
              <List.Item>
                <List.Item.Meta
                  title={<Link to={path}>{title}</Link>}
                  description={dsct || item.node.excerpt}
                />
                <div className="author-info">Posted by {author}</div>
              </List.Item>
            )
          }}
        />
      </div>
    </div>
  );
};

Tags.propTypes = {
  pathContext: PropTypes.shape({
    tag: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      totalCount: PropTypes.number.isRequired,
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            frontmatter: PropTypes.shape({
              path: PropTypes.string.isRequired,
              title: PropTypes.string.isRequired,
            }),
          }),
        }).isRequired
      ),
    }),
  }),
};

export default Tags;

export const pageQuery = graphql`
  query TagPage($tag: String) {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          id
          excerpt(pruneLength: 100)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            path
            title
            author
            dsct
          }
        }
      }
    }
  }
`;


